@import url('https://fonts.googleapis.com/css2?family=Abel&family=Black+Ops+One&family=Poppins:wght@200;300;400;500;600;700;800&display=swap');

:root{
    /** fonts*/
    --ft-1: 'Black Ops One', cursive;
    --ft-2: 'Poppins', sans-serif;
    --ft-3: 'Abel', sans-serif;

    /** default */
    --dark: #101923;
    --bg: #fafafa;
    --light: #fff;
    --green: #1cab9f;
    --blue: #407bff;
    --grys: #b4b4b4;
    --border-dark: rgba(0,0,0,.05);
    --border-light: rgba(255,255,255,.1);
    --red: #d90000;

    /** social */
    --facebook: #1877F2;
    --twitter: #1DA1F2;
    --instagram: #E4405F;
    --youtube: #CD201F;

    /** theme */
    --primary: #02c2a9;
    --secundary: #071844;
    --gradient: linear-gradient(90deg, var(--primary), var(--secundary));

    /** globals */
    font-size: 62.5%;
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    widtH: 100%;
    height: auto;
    min-height: 108rem;
    color: var(--dark);
    background: var(--bg);
    font-family: var(--ft-2);
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

body *{
    font-size: 1rem;
}

:before, 
:after{
    content: '';
    position: absolute;
}

a, span{
    display: inline-block;
}

a{
    text-decoration: none;
}

h1, h2, h3, h4{
    line-height: 120%;
    text-transform: capitalize;
}

ul{
    list-style: none;
}

img{
    width: 100%;
    object-fit: cover;
}

.container{
    width: 96%;
    max-width: 120rem;
    height: auto;
    margin: 0 auto;
}

.flex{
    display: flex;
}