.btn_social{
      width: 4rem;
      height: 4rem;
      display: flex;
      align-items: center;
      justify-content: center;
}

.btn_social *{
      color: var(--light);
      font-size: 2rem;
}

.btn_social.facebook{
      background: var(--facebook);
}

.btn_social.twitter{
      background: var(--twitter);
}

.btn_social.instagram{
      background: var(--instagram);
}

.btn_social.youtube{
      background: var(--youtube);
}