.wrapper_online{
      position: absolute;
      top: 2rem;
      right: 0;
      align-items: center;
      gap: .5rem;
}

.online_img{
      width: 4rem;
      height: 4rem;
      border-radius: 50%;
}

.online_img img{
      width: 3.4rem;
      height: 3.4rem;
      border-radius: 50%;
}

.online_img.green{
      border: solid .3rem var(--green);
}

.online_img.red{
      border: solid .3rem var(--red);
}

.online_status{
      padding: .3rem 1rem;
      border-radius: 999px;
      background: rgba(0,0,0,.5);
}

.online_status span{
      text-transform: uppercase;
      font-weight: 500;
}

.online_status.green{
      color: var(--green);
}

.online_status.red{
      color: var(--red);
}