.box_pagination{
      display: flex;
      align-items: center;
      justify-content: flex-end;
      gap: 4rem;
}

.paginator_items{
      width: fit-content;
      align-items: center;
      justify-content: center;
      gap: 1rem;
}

.paginator_items button{
      border: none;
      outline: none;
}

.paginator_items .prev_page{
      height: 4.2rem;
      padding: 0 2rem;
      background: transparent;
      cursor: pointer;
}

.paginator_items .prev_page *{
      font-size: 2.5rem;
}

.paginator_items .current_page{
      width: 4.2rem;
      height: 4.2rem;
      background: var(--primary);
      color: var(--light);
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
}

.paginator_items .current_page *{
      font-size: 1.6rem;
}

.paginate_amount{
      height: 4.2rem;
      padding: 0 2rem;
      background: var(--light);
      align-items: center;
      border-radius: 999px;
}

.paginate_amount *{
      font-size: 1.4rem;
}