.wrapper_banner{
      width: 100%;
      height: 32.6rem;
      position: relative;
      z-index: 100;
}

.banner_top{
      height: 6rem;
      background: var(--dark);
}

.banner_top .container{
      height: 6rem;
      align-items: center;
}

.logo{
      color: var(--light);
      font-family: var(--ft-1);
      text-transform: uppercase;
      font-size: 2.6rem;
      font-weight: 400;
}

.logo span{
      color: var(--green);
      font-size: 2.6rem;
}

.banner_content{
      height: 24rem;
      width: 100%;
      position: relative;
      overflow: hidden;
}

.banner_img{
      width: 100%;
      height: 100%;
      align-items: center;
      justify-content: center;
      overflow: hidden;
      position: relative;
}

.banner_img:before{
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background: rgba(0,0,0,.3);
}

.banner_img img{
      width: auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
}

.banner_content .container{
      position: absolute;
      height: 100%;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      z-index: 2;
      align-items: center;
      justify-content: center;
      flex-direction: column;
}

.banner_content .container h1{
      font-size: 4rem;
      width: fit-content;
      height: fit-content;
      background: var(--gradient);
      color: transparent;
      background-clip: text;
      max-width: 56rem;
      text-align: center;
      line-height: 100%;
}

.banner_content .container p{
      color: var(--light);
      font-size: 1.4rem;
      width: 50rem;
      text-align: center;
      text-transform:uppercase;
      margin-top: 1rem;
      font-weight: 500;
      text-shadow: .1rem .1rem var(--dark);
}

.banner_data{
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
}

.banner_data .container{
      position: relative;
      height: 100%;
      box-shadow: 0 -7rem var(--dark) inset;
      align-items: flex-end;
}

.data_user{
      width: 27.2rem;
      height: 23.3rem;
      flex-direction: column;
      align-items: center;
      position: relative;
      z-index: 3;
}

.data_user__avatar{
      width: 16.8rem;
      height: 16.4rem;
      border: solid .4rem var(--primary);
      border-bottom: none;
      position: relative;
}

.data_user__avatar:before{
      width: 21rem;
      height: 8rem;
      background: var(--dark);
      border-bottom: solid .4rem var(--primary);
      left: 50%;
      bottom: 0;
      transform: translateX(-50%);
      z-index: -1;
}

.data_user__avatar img{
      width: 16rem;
      height: 16rem;
}

.data_user__info{
      height: 7rem;
      width: 100%;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      color: var(--light);
}

.data_user__info span{
      width: 16.8rem;
      border-bottom: solid .1rem var(--border-light);
      text-transform: uppercase;
      font-size: 1.4rem;
      font-weight: 500;
}

.data_user__info strong{
      font-size: 1.2rem;
      font-weight: 500;
}

.navbar{
      flex: 1;
      height: 7rem;
      background: var(--dark);
}

.navbar .navbar_menu{
      width: 18rem;
      height: 7rem;
}

.navbar .navbar_menu .menu_item{
      display: flex;
      color: var(--light);
      height: 7rem;
      position: relative;
      width: 18rem;
      align-items: center;
      justify-content: center;
      cursor: pointer;
}

.navbar .navbar_menu .menu_item span{
      display: flex;
      align-items: center;
      gap: 1rem;
      text-transform: uppercase;
      font-size: 1.4rem;
      width: 18rem;
      height: 7rem;
      justify-content: center;
      cursor: pointer;
      position: relative;
      z-index: 2;
}

.navbar .navbar_menu .menu_item:hover{
      background: var(--primary);
}

.navbar .navbar_menu .menu_item span *{
      font-size: 2rem;
}

.navbar .navbar_menu .menu_item .navbar_submenu{
      position: absolute;
      top: 100%;
      width: 24rem;
      height: fit-content;
      background: var(--dark);
      left: 0;
      display: none;
}

.navbar .navbar_menu .menu_item:hover .navbar_submenu{
      display: block;
}

.navbar .navbar_menu .menu_item .navbar_submenu li,
.navbar .navbar_menu .menu_item .navbar_submenu li button{
      height: 5rem;
      width: 100%;
}

.navbar .navbar_menu .menu_item .navbar_submenu li button{
      border: none;
      outline: none;
      border-radius: none;
      background: var(--dark);
      color: var(--light);
      text-transform: uppercase;
      text-align: left;
      padding-left: 2.5rem;
      font-size: 1.2rem;
      cursor: pointer;
}

.navbar .navbar_menu .menu_item .navbar_submenu li button:hover{
      background: var(--primary);
}

.float_menu{
      position: absolute;
      height: 4rem;
      right: 2.5rem;
      bottom: 5rem;
      align-items: center;
      gap: 2.5rem;
      z-index: 10;
}

.web_menu{
      height: 3.6rem;
      gap: 1rem;
      align-items: center;
}

.web_menu a{
      width: 12rem;
      height: 3.6rem;
      color: var(--light);
      text-align: center;
      text-transform: uppercase;
      line-height: 3.6rem;
      border-radius: .3rem;
      font-size: 1.2rem;
}

.web_menu a.chat{
      background: var(--blue);
}

.web_menu a.web{
      background: var(--green);
}

.social_menu{
      gap: 1rem;
}