.template{
      width: 100%;
      height: 100vh;
      min-height: 108rem;
      position: relative;
}

.main_content{
      margin-top: 3rem;
}

.header_page{
      height: 6rem;
      align-items: center;
      justify-content: space-between;
}

.title_header{
      font-size: 4.2rem;
      width: fit-content;
      height: 5rem;
      position: relative;
}

.title_header:before{
      width: 3rem;
      height: .5rem;
      background: var(--gradient);
      left: calc(50% - 1.5rem);
      bottom: 0;
}

.gif_all{
      align-items: center;
}

.gif_all img{
      width: 3rem;
      height: 3rem;
}

.gif_all span{
      text-transform: uppercase;
      color: var(--grys);
      font-size: 1.4rem;
}

.gif_all span strong{
      font-size: 1.4rem;
}

.wrapper_gifs{
      margin-top: 3rem;
}

.gif_page{
      flex-wrap: wrap;
      gap: 2rem;
      justify-content: space-around;
}

.LazyLoad {
      opacity: 0;
      transition: opacity 1s ease-in-out;
      margin-bottom: 3rem;
}

.is-visible {
      opacity: 1;
}

.card_box__gif,
.card_load,
.card_gifs{
      width: 18.8rem;
      height: 24rem;
      background: var(--light);
      position: relative;
}

.card_load{
      background: #f4f4f4;
      flex-direction: column;
      align-items: center;
      padding: 3rem;
      position: absolute;
      top: 0;
      left: 0;
      z-index: 10;
}

.card_load > div{
      position: relative;
      background: #eee;
      overflow: hidden;
}

.card_load__img{
      width: 8rem;
      height: 8rem;
}

.load_model__header{
      width: 8rem;
      height: 1rem;
      margin-top: 1.5rem;
}

.load_model__amount{
      width: 12rem;
      height: 3rem;
      margin-top: .5rem;
}

.card_load__price{
      width: 14rem;
      height: 3rem;
      margin-top: 1rem;
}

.card_load > div:before{
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background: linear-gradient(to right, #eee 10%, #ddd 50%, #eee 90%);
      animation: swipeLoad 1s cubic-bezier(0.4, 0.0, 0.2, 1) infinite;
}

@keyframes swipeLoad{
      0%{
            left: -100%;
      }
      100%{
            left: 100%;
      }
}

.card_gifs:before{
      width: 100%;
      height: .5rem;
      top: 0;
      left: 0;
      background: linear-gradient(90deg, var(--primary) 50%, var(--dark) 50%);
}

.card_gif__img{
      width: 100%;
      height: 13.7rem;
      align-items: center;
      justify-content: center;
      position: relative;
      z-index: 2;
}

.gif_img{
      position: relative;
      background: var(--dark);
      position: relative;
}

.gif_img,
.gif_img img,
.gif_img:before,
.gif_img:after{
      width: 8rem;
      height: 8rem;
}

.gif_img:before{
      top: 0;
      left: 0;
      background: var(--primary);
      transform: translate(.5rem, .5rem);
      transition: .3s ease-in-out;
      z-index: -1;
}

.gif_img:after{
      top: 0;
      left: 0;
      background: #000;
      transform: translate(1rem, 1rem);
      z-index: -2;
      filter: blur(15px);
      opacity: .4;
}

.card_gifs:hover .gif_img:before{
      transform: translate(-.5rem, -.5rem);
}

.card_gif__model{
      widtH: 14.8rem;
      height: 5.7rem;
      margin-left: calc(50% - 7.4rem);
      flex-direction: column;
      align-items: center;
      justify-content: center;
      border-top: solid .1rem var(--border-dark);
}

.card_gif__model span{
      text-transform: uppercase;
      font-weight: 500;
      height: fit-content;
}

.card_gif__model strong{
      font-size: 3rem;
      line-height: 2.5rem;
      color: var(--primary);
}

.card_gif__price{
      width: 100%;
      height: 4.6rem;
      background: var(--dark);
      align-items: center;
      gap: 1rem;
}

.gif_price__icon{
      width: 4.6rem;
      height: 100%;
      background: var(--primary);
      align-items: center;
      justify-content: center;
}

.gif_price__icon *{
      font-size: 2.5rem;
}

.card_gif__price strong{
      color: var(--primary);
      font-size: 2.5rem;
}

.card_gif__price strong small{
      text-transform: uppercase;
}

.paginator{
      margin-top: 5rem;
}

.wrapper_load__gifs{
      width: 100%;
      height: 100%;
      min-height: 40rem;
      align-items: center;
      justify-content: center;
}

.load_circle{
      width: 6rem;
      height: 6rem;
      position: relative;
      box-shadow: 0 0 0 .2rem rgba(0,0,0,.05);
      border-radius: 50%;
}

.load_circle:before{
      width: 6rem;
      height: 6rem;
      top: -.1rem;
      left: -.1rem;
      border: solid .2rem var(--primary);
      border-radius: 50%;
      border-right: solid .2rem transparent;
      border-bottom: solid .2rem transparent;
      border-left: solid .2rem transparent;
      animation: spin_load .5s linear infinite;
}

@keyframes spin_load{
      100%{
            transform: rotate(360deg);
      }
}

.empty_gif{
      flex-direction: column;
      align-items: center;
      justify-content: center;
      min-height: 40rem;
}

.empty_icon *{
      color: var(--primary);
      font-size: 10rem;
}

.empty_gif h3{
      font-size: 2rem;
}

.footer_main{
      flex-direction: column;
      align-items: center;
      justify-content: center;
      background: var(--dark);
      width: 100%;
      height: 8rem;
      margin-top: 5rem;
}

.footer_main img{
      height: 2.1rem;
      width: 3.8rem;
}

.footer_main p{
      color: var(--light);
      font-size: 1.2rem;
}